import * as React from 'react'
import { MdKeyboardArrowLeft, MdKeyboardArrowRight, MdKeyboardDoubleArrowLeft, MdKeyboardDoubleArrowRight } from "react-icons/md";
import { RxDotsHorizontal } from "react-icons/rx";
import { Select, Option, Button, IconButton } from "@material-tailwind/react";

type T_PaginationNav = {
  indexData: any[];
  setPayload: (value: any) => void;
  payload: any;
  selectMenu: string;
  setSelectMenu: (value: string) => void;
  maxData: number;
  loadingState?: boolean;
}

const PaginationNav: React.FC<T_PaginationNav> = ({indexData, setPayload, payload, selectMenu, setSelectMenu, maxData, loadingState}) => {
  const onPrev = (toFirst: boolean = false) => {
    if (payload.page > 1) {
      if (toFirst === true) return setPayload((prev) => ({ ...prev, page: 1 }));

      setPayload((prev) => ({ ...prev, page: prev.page - 1 }));
    }

  };

  const onNext = (toLast: boolean = false) => {
    if (payload.page < Math.ceil(maxData / parseInt(selectMenu))) {
      if (toLast === true) return setPayload((prev) => ({ ...prev, page: Math.ceil(maxData / parseInt(selectMenu)) }));

      setPayload((prev) => ({ ...prev, page: prev.page + 1 }));
    }

  };

  const setPage = (page: number) => {
    if (page !== payload.page) {
      setPayload((prev) => ({ ...prev, page}));
    }

    return;
  };

  const renderPagination = () => {
    const maxPage = Math.ceil(maxData/parseInt(selectMenu));
    const arr = [...Array(maxPage)];

    if(Math.ceil(maxData/parseInt(selectMenu)) <= 4) {
      return arr.map((_, i) => (
        <IconButton
          key={i}
          variant={payload.page === i + 1 ? "filled" : "text"}
          color="gray"
          className={payload.page === i + 1 ? 'bg-gray-100 text-black' : ''}
          onClick={() => setPage(i + 1)}
        >
          {i + 1}
        </IconButton>
      ))
    }

    return (
      <>
        <IconButton
          variant={payload.page === 1 ? "filled" : "text"}
          color="gray"
          className={payload.page === 1 ? 'bg-gray-100 text-black' : ''}
          onClick={() => setPage(1)}
        >
          1
        </IconButton>

        {payload.page > 3 && <Button
          variant="text"
          className="p-2"
        >
          <RxDotsHorizontal size={20} />
        </Button>}

        {arr.map((_, i) => {
          if (i + 1 > 1 && i + 1 < maxPage) {
            return ((i + 1 === payload.page - 1 || i + 1 === payload.page || i + 1 === payload.page + 1) &&
              <IconButton
                variant={payload.page === i + 1 ? "filled" : "text"}
                color="gray"
                className={payload.page === i + 1 ? 'bg-gray-100 text-black' : ''}
                onClick={() => setPage(i + 1)}
              >
                {i + 1}
              </IconButton>
            )
          };
        })}

        {payload.page < maxPage - 2 && <Button
          variant="text"
          className="p-2"
        >
          <RxDotsHorizontal size={20} />
        </Button>}

        <IconButton
          variant={payload.page === maxPage ? "filled" : "text"}
          color="gray"
          className={payload.page === maxPage ? 'bg-gray-100 text-black' : ''}
          onClick={() => setPage(maxPage)}
        >
          {maxPage}
        </IconButton>
      </>
    )
  };

  return (
    <div className="flex flex-wrap gap-x-2 gap-y-4 items-center justify-center sm:justify-between">
      <div className="flex flex-wrap gap-x-2 items-center">
        <span className="text-nowrap">Rows per page</span>

        <Select 
          variant="outlined"
          color="blue"
          value={selectMenu}
          className="!border !border-gray-300 focus:!border-blue-700"
          labelProps={{className: 'hidden' }}
          containerProps={{className: '!w-[65px] !min-w-0'}}
          onChange={(e) => setSelectMenu(e)}
          disabled={loadingState}
        >
          <Option className="text-center" value="5">5</Option>
          <Option className="text-center" value="10">10</Option>
          <Option className="text-center" value="15">15</Option>
          <Option className="text-center" value="20">20</Option>
        </Select>

        <span className="text-gray-400 text-nowrap">
          Showing {payload.page > 1 ? parseInt(selectMenu) * (payload.page - 1) + 1 : 1}-{parseInt(selectMenu) * (payload.page - 1) + indexData.length} of {maxData}
        </span>
      </div>

      <div className="flex gap-x-2 items-center">
        <Button
          variant="text"
          disabled={(payload.page <= 1) || loadingState}
          className="p-2"
          onClick={() => onPrev(true)}
        >
          <MdKeyboardDoubleArrowLeft size={20} />
        </Button>
        <Button
          variant="text"
          disabled={(payload.page <= 1) || loadingState}
          className="p-2"
          onClick={onPrev}
        >
          <MdKeyboardArrowLeft size={20} />
        </Button>
        
        {renderPagination()}
        
        <Button
          variant="text"
          disabled={(payload.page >= Math.ceil(maxData / parseInt(selectMenu)) || loadingState)}
          className="p-2"
          onClick={onNext}
        >
          <MdKeyboardArrowRight size={20} />
        </Button>
        <Button
          variant="text"
          disabled={(payload.page >= Math.ceil(maxData / parseInt(selectMenu)) || loadingState)}
          className="p-2"
          onClick={() => onNext(true)}
        >
          <MdKeyboardDoubleArrowRight size={20} />
        </Button>
      </div>
    </div>
  );
}

export default PaginationNav