import * as React from 'react'
import { Button, Card, Typography } from "@material-tailwind/react";
import { IoMdAdd } from "react-icons/io";
import { T_FormModalState } from '../../utils/helpdesk.type';

type T_HelpdeskHeader = {
  title: string;
  buttonAction: (input?: any) => void | React.Dispatch<React.SetStateAction<T_FormModalState>>;
}

const HelpdeskHeader: React.FC<T_HelpdeskHeader> = ({title, buttonAction}) => {
  return (
    <Card className="flex flex-row w-full py-5 px-4 gap-x-4 shadow-none items-center justify-between">
      <Typography
        variant='paragraph'
        className='p-[10px] text-black font-medium bg-gradient-to-r from-blue-50 to-gray-100 rounded-lg'
      >
        Helpdesk : <span className='font-bold'>{title}</span>
      </Typography>
    </Card>
  )
}

export default HelpdeskHeader