import * as React from "react";
import { T_PreviewModalState } from "../../utils/helpdesk.type";
import { Button, Dialog, Typography } from "@material-tailwind/react";

type T_PreviewModal = {
  modalState: T_PreviewModalState;
  setModalState: React.Dispatch<React.SetStateAction<T_PreviewModalState>>
  className?: string;
  html_template: string;
};

const PreviewModal: React.FC<T_PreviewModal> = ({modalState, setModalState, className, html_template}) => {
  return (
  <Dialog open={modalState.visible} size="lg">
      <div className={"flex flex-col p-[35px] pt-[50px] gap-y-[20px] text-black " + className}>
        <iframe
          srcDoc={html_template}
          height={500}
        />
        <hr />
        <div className="flex w-full gap-2 justify-end">
          <Button
            color="gray"
            variant="outlined"
            size="lg"
            className="px-20 text-base border-gray-300 normal-case"
            onClick={() => setModalState({ ...modalState, visible: false })}
          >
            Cancel
          </Button>
        </div>
      </div>
    </Dialog>
  );
};

export default PreviewModal;
