import * as React from "react";
import { useEffect, useState } from "react";
import { MdOutlineFileDownload } from "react-icons/md";
import { FaRegEye } from "react-icons/fa6";
import HelpdeskHeader from "../../../components/HelpdeskHeader";
import Table from "../../../components/Table";
import SearchBar from "../../../components/SearchBar";
import ConfirmationModal from "../../../components/ConfirmationModal";
import { Card, Button, Tooltip, Select, Option } from "@material-tailwind/react";
import { T_Payloads, T_PreviewModalState } from "../../../utils/helpdesk.type";
import service from "../../../../../utils/services";
import { helpdeskDataList } from '../../../../../utils/options';
import { toast } from 'react-toastify';
import PreviewModal from "../../../components/PreviewModal";

type T_LogContent = {
  id: string;
  theme: string;
  mail_address: string;
  mail_subject: string;
  mail_text: string;
};

type T_Log = {
  id: string;
  theme: string;
  mail_address: string;
  mail_subject: string;
  status: string;
  finish_at: string | null;
  company_name: string;
  thirdparty_status: string | null;
};

type T_LogRender = {
  id: string;
  view: JSX.Element;
  theme: string;
  mail_address: string;
  mail_subject: string;
  status: JSX.Element;
  finish_at: string | null;
  company_name: string;
  thirdparty_status: JSX.Element | null;
};

const SettingMailEngine: React.FC = () => {
  const [payloadList, setPayloadList] = useState<T_Payloads>({search: "", page: 1, size: 10, sort: "", theme: null, company_id: null});
  const [logList, setLogList] = useState<T_Log[]>([]);
  const [logListRender, setLogListRender] = useState<T_LogRender[]>([]);
  const [totalData, setTotalData] = useState<number>(0);
  const [selectedData, setSelectedData] = useState<T_LogContent>({id: '', theme: '', mail_address: '', mail_subject: '', mail_text: ''});
  const [loading, setLoading] = useState<boolean>(false);

  const [previewModal, setPreviewModal] = useState<T_PreviewModalState>({visible: false});

  const titles: string[] = ["View", "Theme", "Mail Address", "Subject", "Status", "Finish at", "Company Name", "Third Party Status"];
  const keyLists: string[] = ["view", "theme", "mail_address", "mail_subject", "status", "finish_at", "company_name", "thirdparty_status"];

  useEffect(() => {
    getLogDataList();
  }, [payloadList]);

  useEffect(() => {
    setLogListRender(
      logList.map((item) => {
        return {
          ...item,
          view: (
            <div className="flex justify-center">
              <Tooltip content="Show Mail Template">
                <Button
                  variant="filled"
                  color="blue"
                  className="px-3 py-1 bg-blue-700 text-white hover:bg-blue-500"
                  onClick={() => {
                    getLogDataById(item.id);
                    setPreviewModal({visible: true});
                  }}
                >
                  <FaRegEye size={20} />
                </Button>
              </Tooltip>
            </div>
          ),
          status: (
            <div className={("p-2 py-0.5 w-30 rounded-md text-center font-bold ") + (item.status === "success" ? "bg-green-50 text-green-700" : item.status === "pending" ? "bg-orange-50 text-orange-700" : "bg-red-50 text-red-700")}>
              {item.status === "success" ? "Success" : item.status === "pending" ? "Pending" : "Failed"}
            </div>
          ),
          thirdparty_status: (item.thirdparty_status === null ? null :
            <div className={("p-2 py-0.5 w-30 rounded-md text-center font-bold ") + (item.status === "success" ? "bg-green-50 text-green-700" : item.status === "pending" ? "bg-orange-50 text-orange-700" : "bg-red-50 text-red-700")}>
              {item.status === "success" ? "Success" : item.status === "pending" ? "Pending" : "Failed"}
            </div>
          ),
        };
      })
    );
  }, [logList]);

  useEffect(() => {
    console.log(selectedData);
  }, [selectedData]);

  const getLogDataList = async () => {
    try {
      setLoading(true);
      const response = await helpdeskDataList.log_mail(payloadList);
      console.log(response.data);
      
      setLogList(response.data);
      setTotalData(response.totalData);
      setLoading(false);
    } catch (error) {
      console.log(error.response.message);
      toast.error(error.response.message);
    }
  };

  const getLogDataById = async (id: string) => {
    try {
      setLoading(true);
      const response = await service.get(`/helpdesk/log_mail/${id}`);
      setSelectedData((prev) => ({...prev, mail_text: ''}));
      setSelectedData(response[0]);
      setLoading(false);

    } catch (error) {
      console.log(error.response.message);
      toast.error(error.response.message);
    }
  }

  return (
    <div className="flex flex-col gap-y-4">
      <HelpdeskHeader title="Log Mail" buttonAction={() => {}}/>

      <Card className="flex flex-col px-4 py-8 gap-5 shadow-none">
        <div className="flex flex-row flex-wrap sm:flex-nowrap gap-2">
          <SearchBar setPayload={setPayloadList} loadingState={loading}/>

          <Button
            variant="outlined"
            color="gray"
            className="flex w-full sm:max-w-fit gap-x-1 py-2 px-4 normal-case border-gray-300 items-center justify-center hover:bg-gray-100"
          >
            <MdOutlineFileDownload size={20} />
            Download List Log Mail
          </Button>
        </div>

        <Table
          indexList={titles}
          indexKey={keyLists}
          indexData={logListRender}
          maxData={totalData}
          payload={payloadList}
          setPayload={setPayloadList}
          loadingState={loading}
        />

        <PreviewModal
          modalState={previewModal}
          setModalState={setPreviewModal}
          html_template={selectedData.mail_text}
        />
      </Card>
    </div>
  );
};

export default SettingMailEngine;

