import * as React from "react";
import { useState, useEffect } from "react";
import { TiArrowSortedDown, TiArrowSortedUp } from "react-icons/ti";
import { Card } from "@material-tailwind/react";
import TableSkeleton from "./skeleton";
import PaginationNav from "../PaginationNav";

type T_Table = {
  indexList: string[];
  indexKey: string[];
  indexData: any[];
  setPayload?: (value: any) => void;
  payload?: any;
  maxData?: number;
  loadingState?: boolean;
  usePagination?: boolean;
  striped?: boolean;
  className?: string;
};
 
const Table: React.FC<T_Table> = ({indexList, indexKey, indexData, setPayload, payload, maxData, loadingState, usePagination = true, striped = false, className}) => {
  const [selectMenu, setSelectMenu] = useState<string>("10");
  const [activeSort, setActiveSort] = useState<string>("");

  useEffect(() => {
    if(payload) {
      sortData("");
    }
  }, []);

  useEffect(() => {
    if(payload) {
      setPayload((prev) => {
        if (prev.page === 1) return { ...prev, size: parseInt(selectMenu) };
  
        if (prev.page > Math.ceil(maxData / parseInt(selectMenu))) {
          return {
            ...prev,
            size: parseInt(selectMenu),
            page: Math.ceil(maxData / parseInt(selectMenu)),
          };
        }
  
        return { ...prev, size: parseInt(selectMenu) };
      });
    }
  }, [selectMenu]);

  const sortData = (key, name?) => {
    if (payload && !key.length) return setPayload((prev) => ({ ...prev, sort: key }));
    if (payload && !name) return setPayload((prev) => ({ ...prev, sort: key + " asc" }));

    if (payload && activeSort === name) {
      setPayload((prev) => ({
        ...prev,
        sort: prev.sort.split(" ")[1] === "asc" ? key + " desc" : key + " asc",
      }));
    } else {
      setPayload((prev) => ({ ...prev, sort: key + " asc" }));
    }
  };

  const renderSkeleton = () => {
    const arr = [...Array(parseInt(selectMenu))]
    
    const skeleton = [...arr].map((_, i) => (
      <tr key={i} className={i === parseInt(selectMenu) - 1 ? "" : "border-b border-blue-gray-50"}>
        <td key="index" className="p-2">
          <TableSkeleton key={"index"}/>
        </td>
        {indexList.map((_, j) => (
          <td key={j} className="p-3">
            <TableSkeleton key={j}/>
          </td>
        ))}
      </tr>
    ));
    
    return skeleton;
  };

  return (
    <>
      <Card className={"h-full w-full border shadow-none overflow-auto " + className}>
        <table className="w-full min-w-max table-auto text-left">
          <thead className="">
            <tr>
              <th className="w-5 text-center border-b border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors first:rounded-ss-xl last:rounded-se-xl">
                No
              </th>
              {indexList.map((index, i) => {
                if (index === "Action" || index === "View") {
                  return (
                    <th key={i} className={("w-14 text-center border-b border-blue-gray-100 bg-blue-gray-50/50 py-4 px-10 transition-colors first:rounded-ss-xl last:rounded-se-xl ") + (index === "Action" && "border-l")}>
                      {index}
                    </th>
                  );
                }

                return (
                  <th
                    key={i}
                    className={("border-b border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors first:rounded-ss-xl last:rounded-se-xl ") + (payload && "cursor-pointer hover:bg-blue-gray-50 ") + ((index === "Status") && "w-14")}
                    onClick={() => {
                      if(payload) {
                        sortData(indexKey[i], index);
                        setActiveSort(index);
                      }
                    }}
                  >
                    <span className={activeSort === index ? "flex gap-x-2 text-blue-500" : "flex gap-x-2"}>
                      {index}
                      {payload && (activeSort !== index ||
                        payload.sort.split(" ")[1] === "asc") && (
                        <TiArrowSortedDown
                          size={20}
                        />
                      )}
                      {payload && activeSort === index &&
                        payload.sort.split(" ")[1] === "desc" && (
                          <TiArrowSortedUp
                            size={20}
                            className={
                              activeSort === index
                                ? "cursor-pointer text-blue-500"
                                : "cursor-pointer hover:text-gray-500"
                            }
                          />
                        )}
                    </span>
                  </th>
                );
              })}
            </tr>
          </thead>
          <tbody>
            {loadingState  
            ? renderSkeleton()
            : indexData.length !== 0
              ? indexData.map((data, i) => (
                <tr key={i} className={(i === indexData.length - 1 ? "" : "border-b border-blue-gray-50 ") + (striped && "even:bg-blue-gray-50/10")}>
                  {payload
                  ? <td className="text-center">
                      {parseInt(selectMenu) > 1 ? i + 1 + parseInt(selectMenu) * (payload.page - 1) : i + 1}
                    </td>
                  : <td className="text-center">
                      {i + 1}
                    </td>
                  }

                  {indexKey.map((key, j) => {
                    const value = data[key];
                    if (key === "action") {
                      return (
                        <td key={j} className="w-fit border-l border-blue-gray-50">
                          {value ? value : "-"}
                        </td>
                      );
                    }

                    return (
                      <td key={j} className="p-3 text-wrap">
                        {value ? value : "-"}
                      </td>
                    );
                  })}
                </tr>
              ))
              : (
                <>
                  <tr>
                    <td className="p-4">-</td>
                    <td className="p-4">No data found</td>
                  </tr>
                </>
              )
            }
          </tbody>
        </table>
      </Card>

      {payload && usePagination && <PaginationNav
        indexData={indexData}
        payload={payload}
        setPayload={setPayload}
        selectMenu={selectMenu}
        setSelectMenu={setSelectMenu}
        maxData={maxData}
        loadingState={loadingState}
      />}
    </>
  );
}

export default Table;