import * as React from 'react'
import { Typography } from "@material-tailwind/react"

const TableSkeleton: React.FC = () => {
  return (
    <Typography
      as="div"
      variant="paragraph"
      className="w-full h-6 rounded-sm bg-gray-300 animate-pulse"
    >
      &nbsp;
    </Typography>
  )
}

export default TableSkeleton